body {
  margin: 0;
}

.code-area {
  background-color: hsl(0, 0%, 0%);
}

.pane {
  height: 50vh;
  display: flex;
}

.iframe{
  border: none;
  width: 100%;
  height: 100%;
}

.code-editor-container {
  flex-grow: 1;
  flex-basis: 0;
  display: flex;
  flex-direction: column;
  padding: .5rem;
  background-color: hsl(0, 0%, 0%);
}

.code-editor-container.collapsed {
  flex-grow: 0;
}

.code-editor-container.collapsed .CodeMirror-scroll {
  position: absolute;
  overflow: hidden !important;
}

.code-editor-title {
  background-color: rgb(37, 37, 37);
  color: white;
  font-family: Arial, Helvetica, sans-serif;
  padding: 10px 10px 10px 10px;
  
}

.CodeMirror {
  height: 100% !important;
}

.CodeMirrorWrapper {
  flex-grow: 1;
  overflow: hidden;
}

.open-close-btn {
  margin-left: 10px;
  border: none;
  font-family: Arial, Helvetica, sans-serif;
  color: rgb(0, 0, 0);
  background-color: yellow;
  border-radius: 10px;
  cursor: pointer;
}

